<template>
  <div id="loginscreen">
    <div id="box">
      <v-img
        src="../../public/img/textures/TextLogo.png"
        width="50%"
        id="loginImg"
      ></v-img>
      <LoginBox @swap="swapBox" v-if="login" @loginUser="loginUser" />

      <RegisterBox
        :userAccounts="userAccounts"
        @swap="swapBox"
        @registerUser="registerUser"
        v-if="!login"
      />
      <a @click="$router.push('Map')">Dev Login</a>
    </div>
    <v-snackbar color="error" v-model="snackbar">
      <p id="snackbarText" class="text-center">Email or Password is wrong!</p>
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";
import LoginBox from "../components/LoginBox.vue";
import RegisterBox from "../components/RegisterBox.vue";

export default {
  data() {
    return {
      login: true,
      userAccounts: [],
      snackbar: false,
    };
  },
  components: {
    LoginBox,
    RegisterBox,
  },
  methods: {
    async getData() {
      let res = await axios.get("http://localhost:3000/accounts");
      this.userAccounts = res.data;
      console.log(this.userAccounts);
    },
    async registerUser(value) {
      console.log("abschicken");

      var today = new Date();
      var dd = String(today.getDate()).padStart(2, "0");
      var mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
      var yyyy = today.getFullYear();

      today = yyyy + "-" + mm + "-" + dd;

      value.createddate = today;

      console.log(value);

      let res = await axios({
        url: "http://localhost:3000/register",
        method: "post",
        data: value,
      });
      console.log("funkt");
      console.log(res);
      this.getData();
    },
    async loginUser(value) {
      if (
        this.userAccounts.find((el) => el.email == value.email) != undefined &&
        this.userAccounts.find((el) => el.password == value.password) !=
          undefined
      ) {
        
        let res = await axios({
          url: "http://localhost:3000/login",
          method: "post",
          data: value,
          withCredentials: true
        });
        console.log(res);
        this.$router.push("Map");
      } else {
        this.snackbar = true;
        console.log("funkt");
      }
    },
    swapBox() {
      this.login = !this.login;
    },
  },
  created() {
    this.getData();
  },
};
</script>

<style>
#loginscreen {
  width: 100%;
  height: 100%;
  background-image: url("../../public/img/textures/LoginBackgroundMobile.jpg") !important;
  background-size: cover 100%;
  background-position: center;
  background-repeat: no-repeat;
}
#box {
  position: relative;
  margin-right: 3rem;
  margin-left: 3rem;
  top: 40%;
  transform: translateY(-50%);
}
#loginImg {
  margin-bottom: 3rem;
  margin-left: auto;
  margin-right: auto;
}
#snackbarText {
  margin: 0px;
}
/* .error {
  top: 1rem;
  position: sticky;
} */
</style>
