<template>
  <div>
    <div id="top-panel">
      <v-btn
        id="backbtn"
        fab
        x-large
        elevation="3"
        color="white"
        @click="$router.push('Map')"
      >
        <v-icon>keyboard_backspace</v-icon>
      </v-btn>
    </div>
    <div id="search-panel" class="centered">
      <v-text-field
        label="Search"
        v-model="searchbox"
        class="font-bold"
        solo
        elevation="3"
      ></v-text-field>
    </div>
    <div id="btn-panel" class="centered">
      <v-btn
        id="filterbtn"
        x-large
        block
        elevation="3"
        color="white"
        class="font-bold"
        >Filter
      </v-btn>
    </div>
    <div id="list-panel">
      <List class="centered" :listShow="listShow" :items="events" />
    </div>
    <div id="showmore-panel" class="centered">
      <v-btn
        id="showmorebtn"
        x-large
        block
        elevation="3"
        color="white"
        @click="listShow=100"
        >Show More Events
      </v-btn>
    </div>
  </div>
</template>

<script>
import List from "../components/EventList.vue";
import axios from "axios";
export default {
  name: "EventMenu",
  components: {
    List,
  },
  data() {
    return {
      events: [],
      listShow: 10,
      searchbox: "",
    };
  },
  async mounted() {
    await this.refreshEvents();
  },
  methods: {
    //Fetches Events from Server
    async refreshEvents() {
      let res = await axios.get("http://localhost:3000/events");
      this.events = res.data;
      console.log("Fetched Events: " + this.events, this.events);
    },
  },
};
</script>

<style lang="scss" scoped>
#top-panel {
  padding-bottom: 3rem;
}
#search-panel {
  margin-bottom: -1rem;
}
#list-panel {
  width: 100%;
  margin: auto;
  position: sticky;
  padding-top: 1rem;
  padding-bottom:1rem;
}
#backbtn {
  top: 1.5rem;
  left: 1.5rem;
}
#showmorebtn{
  height:2rem;
  font-size:0.8rem;
}
</style>
